"use client";

import { useState } from "react";
import { CalendarClock } from "lucide-react";
import { useRouter } from "next/router";


export default function BookAppointmentPage() {
  const [department, setDepartment] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [payment, setPayment] = useState("");
  const [amount, setAmount] = useState("");

  const departments: { [key: string]: number } = {
    "General Medicine": 0,
    "Dermatology": 2000,
    "Neurology": 3000,
  };

  // Handles department change and updates fee (amount)
  const handleDepartmentChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedDepartment = e.target.value;
    setDepartment(selectedDepartment);
    const selectedFee = departments[selectedDepartment] || 0;
    setAmount(selectedFee.toString());
  };

  const router = useRouter();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Check if essential fields are filled
    if (!department || !date || !time || !payment) {
      alert("Please fill in all required fields.");
      return;
    }

    // Make the API call to book the appointment
    const response = await fetch("http://localhost:5000/api/bookappointment", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ department, date, time, amount, payment }),
    });

    if (response.ok) {
      const data = await response.json();
      
      // After successful booking, redirect to the patient's appointment page
      router.push({
        pathname: "/patientappointments",
        query: {
          department: department.toString(),
          date: date.toString(),
          time: time.toString(),
          amount: amount.toString(),
          status: "Pending", // Default status for new appointments
        },
      });
    } 
    else {
      const err = await response.json();
      alert(err.error || "Booking failed");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-[80vh] bg-gray-50 px-4 pt-20">
      <div className="w-full max-w-xl bg-white rounded-xl shadow p-6 space-y-5">
        <div className="flex items-center justify-center gap-2 mb-4">
          <CalendarClock className="w-7 h-7 text-blue-600" />
          <h2 className="text-3xl font-bold text-gray-800 text-center">
            Book Appointment
          </h2>
        </div>

        <form onSubmit={handleSubmit} className="space-y-5">
          {/* Department */}
          <div>
            <label className="block text-gray-700 font-medium mb-2">
              Select Department
            </label>
            <select
              value={department}
              onChange={handleDepartmentChange}
              className="w-full border border-gray-300 p-3 rounded-lg focus:ring focus:ring-blue-200"
              required
            >
              <option value="">-- Choose Department --</option>
              {Object.keys(departments).map((dep) => (
                <option key={dep} value={dep}>
                  {dep}
                </option>
              ))}
            </select>
          </div>

          {/* Date */}
          <div>
            <label className="block text-gray-700 font-medium mb-2">
              Select Date
            </label>
            <input
              type="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              className="w-full border border-gray-300 p-3 rounded-lg focus:ring focus:ring-blue-200"
              required
            />
          </div>

          {/* Time */}
          <div>
            <label className="block text-gray-700 font-medium mb-2">
              Select Time
            </label>
            <input
              type="time"
              value={time}
              onChange={(e) => setTime(e.target.value)}
              className="w-full border border-gray-300 p-3 rounded-lg focus:ring focus:ring-blue-200"
              required
            />
          </div>

          {/* Amount */}
          <div>
            <label className="block text-gray-700 font-medium mb-2">
              Amount (TSH)
            </label>
            <input
              type="text"
              value={amount === "0" ? "No fee" : amount}
              readOnly
              className="w-full border border-gray-300 p-3 rounded-lg bg-gray-100"
              placeholder="Auto-filled"
              required
            />
          </div>

          {/* Payment (Only show if fee > 0) */}
          {parseInt(amount) > 0 && (
            <div>
              <label className="block text-gray-700 font-medium mb-2">
                Select Payment Method
              </label>
              <select
                value={payment}
                onChange={(e) => setPayment(e.target.value)}
                className="w-full border border-gray-300 p-3 rounded-lg focus:ring focus:ring-blue-200"
                required
              >
                <option value="">-- Choose a Payment Method --</option>
                <option value="M-Pesa">M-Pesa</option>
                <option value="Airtel Money">Airtel Money</option>
              </select>
            </div>
          )}

          {/* Submit Button */}
          <button
            type="submit"
            className="w-full bg-blue-600 text-white font-semibold px-5 py-3 rounded-lg hover:bg-blue-700 transition"
          >
            Confirm
          </button>
        </form>
      </div>
    </div>
  );
}
